@import url(https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap);

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body::-webkit-scrollbar {
  width: 6px;
}

body::-webkit-scrollbar-track {
  background: #f5f7f8;
}

body::-webkit-scrollbar-thumb {
  background: #a6a7a8;
  border-radius: 100px;
}

a {
  text-decoration: none;
}

b > a {
  text-decoration: underline;
  cursor: pointer;
}

body {
  background-color: #edeff0;
}

.nav-link {
  cursor: pointer;
}

.responsive-black {
  display: none;
}

button {
  font-family: "Inter";
}

.lang-xs-btn {
  display: none;
}

.lang-btn {
  margin: 10px;
  cursor: pointer;
  padding: 10px;
  text-align: center;
  border: solid 1px #000000;
  text-decoration: underline;
  border-radius: 100%;
  box-shadow: 3px 3px 0 #000000;
}

.container {
  width: 80%;
  margin: 0 auto;
}

.navbar {
  display: flex;
  height: 15vh;
  align-items: center;
  justify-content: space-between;
  font-family: "Inter", sans-serif;
}

.nav-burger {
  display: none;
  z-index: 1;
}

.navbar > .logoSection {
  display: flex;
  align-items: center;
}

.navbar > .logoSection > img {
  width: 9%;
  margin-right: 15px;
  color: blue;
}

.navbar > .logo {
  letter-spacing: -1px;
  font-size: 25px;
  font-weight: 700;
}

.nav-links {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 400px;
  transition: 0.5s ease-in-out;
  margin: auto;
  z-index: 1;
}

.nav-link {
  list-style-type: none;
  font-size: 16px;
  cursor: pointer;
  padding-bottom: 2px;
}
.nav-link-footer {
  list-style-type: none;
  font-size: 16px;
  cursor: pointer;
}

.nav-link:hover {
  color: #000314;
  background: rgb(219, 219, 219);
  cursor: pointer;
  border-radius: 4px;
}
.nav-link {
  padding: 10px;
  margin-bottom: 2px;
  transition: 0.2s ease-in-out;
}

.contact-btn {
  border: none;
  border: 1px solid #313638;
  background: none;
  font-family: "Inter";
  padding: 13px 23px;
  border-radius: 50px;
  color: #313638;
  font-weight: 600;
  outline: none;
  cursor: pointer;
  font-size: 13px;
  z-index: 1;
}

.header-main {
  font-family: "Inter";
  text-align: center;
  padding: 70px 0;
}

.header-main > .intro {
  font-size: 23px;
  letter-spacing: -1px;
  font-weight: 600;
}

.header-main > .main {
  padding: 20px 0px 30px 0;
  min-width: 90%;
  margin: auto;
  font-size: 65px;
  letter-spacing: -3px;
  background: -webkit-linear-gradient(
    -360deg,
    #ff51a8,
    #1fa0c3,
    #9946f5,
    #ff51a8,
    #5188c5,
    #6b3cda,
    #f47171
  );
  background-size: 400% 200%;
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: flow 25s linear infinite;
}

@keyframes flow {
  0% {
    background-position: 0 100%;
  }
  50% {
    background-position: 200% 100%;
  }
  100% {
    background-position: 0 100%;
  }
}

.header-main > .biography {
  font-weight: 500;
  font-size: 20px;
  letter-spacing: -1px;
  width: 50%;
  margin: auto;
  line-height: 30px;
}

.gradientText {
  color: #3a0ca3;
}

.header-main > .buttons {
  padding: 30px 0;
}

.activeBtn {
  font-family: "Inter";
  display: inline-block;
  margin-right: 10px;
  background: rgb(0, 0, 0);
  border: none;
  padding: 15px 30px;
  border-radius: 50px;
  font-weight: 500;
  color: white;
  font-size: 15px;
  cursor: pointer;
}

.normalBtn {
  font-family: "Inter";
  display: inline-block;
  margin-right: 20px;
  background: none;
  border: 1px solid #000;
  padding: 15px 30px;
  border-radius: 50px;
  font-weight: 500;
  color: #000;
  font-size: 15px;
  cursor: pointer;
}

.header {
  background-color: #f5f7f8;
  min-height: 100vh;
  padding-bottom: 120px;
}

.scroll-down {
  font-weight: 600;
  letter-spacing: -1px;
  margin-top: 40px;
}
.youtubeLink {
  text-decoration: none;
  color: #ff0000;
  border-bottom: 1px solid #ff0000;
}

.mail-link {
  text-decoration: none;
  color: #002fff;
  border-bottom: 1px solid #002fff;
  cursor: pointer;
}
.skills {
  font-family: "Inter";
  padding: 0px 0px 150px 0px;
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.skills > h1 {
  padding-top: 40px;
  font-size: 47px;
  letter-spacing: -2px;
  background-clip: text;
  -webkit-background-clip: text;
  color: transparent;
  background-size: cover;
  background-image: linear-gradient(
    147deg,
    #f57170 0%,
    #8233ed 20%,
    #f57170 40%,
    #5333ed 100%
  );
  text-align: center;
}

.skill-list {
  padding: 60px 40px 0px 40px;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 25px;
  height: 100%;
}

.skill-list > span {
  font-size: 18px;
  font-weight: 600;
  border-radius: 3px;
  background: #e3e5e6;
  transition: 0.2s ease-in-out;
  display: flex;
  align-items: center;
  height: 100%;
  box-shadow: 0px 4px 2px rgba(0, 0, 0, 0.068);
}

.skill-list > span > span {
  background: white;
  padding: 12px 12px;
  display: flex;
  align-items: center;
  height: 100%;
  padding: 20px 18px;
  font-size: 23px;
  color: #9946f5;
}

.skill-list > span > h1 {
  font-size: 18px;
  font-weight: 600;
  padding: 27px 18px;
  letter-spacing: -0.9px;
}

.skill-list > span:hover {
  background: #dee0e1;
  transform: translateX(5px);
}

.skills-text {
  font-weight: 400;
  font-size: 17px;
  width: 60%;
  line-height: 30px;
  padding-top: 20px;
  text-align: center;
  margin: auto;
}

.about-me {
  font-family: "Inter";
  min-height: 70vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #e4e6e7;
  margin-bottom: 100px;
  margin-top: 10px;
  padding: 50px 0;
}

.about-me > h1 {
  font-size: 45px;
  letter-spacing: -2px;
  background-clip: text;
  -webkit-background-clip: text;
  color: transparent;
  background-size: cover;
  background-image: linear-gradient(
    160deg,
    #8233ed 0%,
    #f57170 90%,
    #5333ed 100%
  );
}

.about-me > .aboutText {
  padding-top: 20px;
  font-weight: 500;
  font-size: 12px;
  width: 60%;
  line-height: 2rem;
  color: #000000f3;
}

.p1,
.p2,
.p3 {
  margin-bottom: 20px;
}

.p3 {
  font-size: 14.5px;
}

.p3-author {
  float: right;
}

.project-header {
  font-family: "Inter";
  font-size: 45px;
  letter-spacing: -1px;
  padding-bottom: 10px;
  background-clip: text;
  -webkit-background-clip: text;
  color: transparent;
  background-size: cover;
  background-image: linear-gradient(
    147deg,
    #5333ed 0%,
    #f57170 50%,
    #8233ed 100%
  );
}

.project-text {
  padding: 15px 0;
  width: 60%;
  line-height: 30px;
  font-size: 17px;
  text-align: center;
}

.project-main {
  min-height: 20vh;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  font-family: "Inter";
  padding-bottom: 50px;
}

.projects {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 50px;
  font-family: "Inter";
  margin: 50px 0;
}

.projects > .project {
  background-color: white;
  box-shadow: 2px 5px 38px 16px #d8d8d81c;
  position: relative;
  min-height: 600px;
}

.projects > .project > .project-detail {
  padding: 20px;
}

.projects > .project > .project-detail > h2 {
  font-size: 25px;
  letter-spacing: -1px;
}

.projects > .project > .project-detail > p {
  padding: 10px 0;
  font-weight: 400;
  font-size: 15px;
  line-height: 25px;
}

.projects > .project > .project-detail > .tech-used {
  margin-top: 10px;
}

.projects > .project > .project-detail > .tech-used > span {
  background: rgb(245, 245, 245);
  padding: 5px 12px;
  border-radius: 50px;
  font-size: 14px;
  display: inline-block;
  margin-bottom: 10px;
  margin-right: 10px;
}

.projects > .project > .project-detail > .projectButtons {
  margin-top: 20px;
  display: flex;
}

.projects > .project > .project-detail > .projectButtons > .buttonActive {
  border: none;
  background: rgb(0, 0, 0);
  padding: 5px 10px;
  border-radius: 50px;
  color: white;
  font-weight: 500;
  font-size: 16px;
  margin-right: 10px;
  width: 100%;
  cursor: pointer;
}

.projects > .project > .project-detail > .projectButtons > .buttonNormal {
  border: none;
  border: 1px solid #313638;
  background: none;
  padding: 5px 30px;
  border-radius: 50px;
  color: black;
  font-size: 16px;
  width: 100%;
  cursor: pointer;
  font-weight: 500;
}

.projects > .project > div > img {
  width: 100%;
  object-fit: unset;
  height: 200px;
}

.project-btn {
  position: absolute;
  left: 0;
  right: 0;
  width: 90%;
  bottom: 15px;
  margin: auto;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-column-gap: 10px;
}

.project-btn > a > .liveWebsiteBtn {
  background: #222222;
  border: none;
  width: 100%;
  margin-right: 10px;
  padding: 15px;
  color: white;
  border-radius: 3px;
  cursor: pointer;
  transition: 0.3s ease-in-out;
}

.project-btn > a > .liveWebsiteBtn:hover {
  background-color: #000314;
}

.project-btn > a > .githubBtn {
  width: 100%;
  background: none;
  border: 1px solid gray;
  border-radius: 3px;
  cursor: pointer;
  transition: 0.3s ease-in-out;
  padding: 15px;
}

.project-btn > a > .githubBtn:hover {
  background: rgb(243, 243, 243);
}

/* Footer */
.footer {
  background: rgba(247, 247, 247, 0.479);
  font-family: "Inter";
  padding: 60px 0;
}

.footer-container {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
}

.footer-container > .logoSection > h1 {
  font-size: 23px;
  font-family: "Inter";
}

.footer-container > .logoSection > p {
  font-size: 15px;
  margin-top: 10px;
  line-height: 1.6rem;
}

.social-media {
  display: flex;
  flex-direction: column;
}

.social-media-header {
  font-weight: 600;
  font-size: 14px;
  text-transform: uppercase;
}

.social-media-links {
  display: flex;
  flex-direction: column;
  margin-top: 20px;
}

.social-media-links > a {
  margin-bottom: 15px;
  text-decoration: none;
  color: #4f5b86;
  font-weight: 600;
  font-size: 16px;
  letter-spacing: -0.4px;
  transition: 0.3s ease-in-out;
}

.social-media-links > a:hover {
  color: #222222;
  transition: all 0.2s ease-in-out;
}

.footer-links {
  display: flex;
  flex-direction: column;
}

.footer-links-header {
  font-weight: 600;
  font-size: 14px;
  text-transform: uppercase;
}

.footer-links-link {
  display: flex;
  flex-direction: column;
  margin-top: 20px;
  max-width: 85%;
}

.footer-links-link > a {
  margin-bottom: 15px;
  text-decoration: none;
  color: #4f5b86;
  font-weight: 600;
  font-size: 16px;
  letter-spacing: -0.4px;
}

.footer-links-link > a:hover {
  color: #222222;
  transition: all 0.2s ease-in-out;
}

.buttonActive {
  display: inline-block;
  border: none;
  background: #000;
  padding: 10px 20px;
  border-radius: 50px;
  color: white;
  font-weight: 500;
  font-size: 12px;
  margin-top: 15px;
  margin-right: 10px;
  cursor: pointer;
}

@media only screen and (max-width: 1500px) {
  .projects > .project > div > img {
    width: 100%;
    object-fit: unset;
    height: 170px;
  }

  .header-main > .main {
    min-width: 100%;
    padding: 10px 0px 20px 0;
  }

  .projects {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-column-gap: 50px;
  }

  .about-me {
    min-height: 60vh;
  }

  .about-me > .aboutText {
    padding-top: 20px;
    font-weight: 500;
    font-size: 16px;
    width: 65%;
    line-height: 1.9rem;
    color: #000000f3;
  }

  .skill-list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 25px;
    height: 100%;
  }

  .skills-text {
    font-weight: 400;
    font-size: 17px;
    width: 100%;
    line-height: 30px;
    padding-top: 20px;
    text-align: justify;
    margin: auto;
  }

  .projects > .project > .project-detail > .tech-used > span {
    font-size: 13px;
    margin-right: 3px;
  }

  .footer {
    padding-bottom: 40px;
  }
}

/* Tablet max */
@media only screen and (max-width: 1024px) {
  .header-main > .main {
    font-size: 53px;
  }

  .projects {
    grid-template-columns: repeat(2, 1fr);
    grid-column-gap: 100px;
  }
  .project-text {
    width: 80%;
  }
  .about-me > .aboutText {
    font-size: 18px;
    font-weight: 500;
    min-width: 70%;
    line-height: 1.9rem;
  }

  .nav-links {
    width: 350px;
    font-size: 13px;
  }
}

/* Ipad */
@media only screen and (max-width: 768px) {
  .container {
    width: 90%;
  }

  .nav-links {
    width: 250px;
  }

  .nav-links > li {
    font-size: 15px;
  }

  .contact-btn {
    border: none;
    border: 1px solid #313638;
    background: none;
    font-family: "Inter";
    padding: 13px 20px;
    border-radius: 50px;
    color: #313638;
    font-weight: 400;
    outline: none;
    font-size: 13px;
  }

  .navbar > .logo {
    font-size: 26px;
  }

  .header-main > .main {
    font-size: 45px;
  }

  .header-main > .biography {
    font-weight: 500;
    font-size: 16px;
    letter-spacing: -1px;
    width: 60%;
    margin: auto;
    line-height: 30px;
  }

  .skill-list {
    grid-template-columns: repeat(2, 1fr);
  }

  .skills-text {
    font-weight: 400;
    font-size: 16px;
    width: 90%;
    line-height: 30px;
    padding-top: 20px;
    text-align: center;
    margin: auto;
  }

  .project-text {
    padding: 15px 0;
    width: 90%;
    line-height: 30px;
    font-size: 16px;
    text-align: center;
  }

  .projects {
    grid-column-gap: 50px;
  }

  .about-me > .aboutText {
    font-size: 16px;
    font-weight: 400;
    width: 80%;
    letter-spacing: -0.4px;
  }

  .footer-container > .logoSection > p {
    font-size: 13px;
  }

  .footer-links-link > a {
    margin-bottom: 10px;
    text-decoration: none;
    color: #4f5b86;
    font-weight: 600;
    font-size: 14px;
    letter-spacing: -0.4px;
  }

  .social-media-links > a {
    margin-bottom: 15px;
    text-decoration: none;
    color: #4f5b86;
    font-weight: 600;
    font-size: 14px;
    letter-spacing: -0.4px;
    transition: 0.3s ease-in-out;
  }

  .footer-links-header,
  .social-media-header {
    font-size: 13px;
  }
}

/* Mobile max */
@media only screen and (max-width: 699px) {
  .project-btn > a > .liveWebsiteBtn {
    font-size: 12px;
  }

  .header-main > .main {
    font-size: 43px;
  }

  .responsive-navbar {
    display: flex;
    justify-content: space-between;
    transition: 1s ease-in-out;
  }

  .logo {
    flex: 1 1;
  }

  .nav-links {
    display: none;
    transform: translateX(300px);
    transition: 0.3s ease-in-out;
    display: flex;
    flex-direction: column;
    height: 10%;
    background-color: white;
    position: fixed;
    height: 100vh;
    right: 0;
    top: 0%;
    padding: 100px 0;
    z-index: 2;
  }

  .nav-burger {
    display: block;
    font-size: 24px;
    margin-left: 10px;
  }

  .contact-btn {
    border: none;
    border: 1px solid #313638;
    background: none;
    font-family: "Inter";
    padding: 8px 18px;
    border-radius: 50px;
    color: #313638;
    font-weight: 600;
    outline: none;
    font-size: 11px;
    margin-left: 10px;
  }

  .responsive-black {
    display: block;
    position: fixed;
    background-color: rgba(0, 0, 0, 0.315);
    height: 100vh;
    width: 100%;
    top: 0;
    left: 0;
    z-index: 1;
  }

  .projects {
    grid-column-gap: 20px;
  }
}

@media only screen and (max-width: 540px) {
  .header-main {
    text-align: left;
  }

  .header-main > .main {
    font-size: 30px;
    letter-spacing: -1px;
  }

  .header-main > .biography {
    width: 100%;
    font-size: 16px;
    font-weight: 400;
    letter-spacing: -0.4px;
  }

  .skills > .skills-header {
    font-size: 30px;
    text-align: left;
  }

  .skills > .skills-text {
    font-size: 15px;
    width: 100%;
    text-align: left;
  }

  .skill-list {
    padding: 30px 0;
  }

  .skill-list > span > h1 {
    font-size: 16px;
  }

  .project-main > .project-header {
    text-align: left;
  }

  .project-text {
    text-align: left;
    width: 100%;
    font-size: 15px;
  }

  .footer-container {
    display: flex;
    flex-direction: column;
  }

  .skill-list > span > h1 {
    font-size: 15px;
  }

  .footer-links {
    padding-top: 20px;
  }

  .footer-links-link > a,
  .social-media-links > a {
    margin-bottom: 20px;

    max-width: 80%;
  }

  .about-me {
    align-items: initial;
    background: none;
  }

  .about-me > .aboutText {
    width: 100%;
    font-size: 15px;
  }

  .social-media {
    margin-top: 35px;
  }

  .projects {
    grid-template-columns: repeat(1, 1fr);
    padding: 0px 50px;
  }
}

@media only screen and (max-width: 500px) {
  .header-main {
    text-align: left;
  }

  .header-main > .main {
    font-size: 35px;
  }

  .header-main > .biography {
    width: 100%;
    font-size: 16px;
    font-weight: 400;
    letter-spacing: -0.4px;
  }

  .skills > .skills-header {
    font-size: 30px;
    text-align: left;
  }

  .skills > .skills-text {
    font-size: 15px;
    width: 100%;
    text-align: left;
  }

  .skill-list {
    padding: 30px 0;
  }

  .skill-list > span > h1 {
    font-size: 16px;
  }

  .project-text {
    text-align: left;
    width: 100%;
    font-size: 15px;
  }

  .projects {
    grid-template-columns: repeat(1, 1fr);
    padding: 0px 30px;
  }

  .footer-container {
    display: flex;
    flex-direction: column;
  }

  .social-media {
    margin-top: 35px;
  }

  .about-me {
    align-items: initial;
    background: none;
  }

  .about-me > .aboutText {
    width: 100%;
    font-size: 15px;
  }

  .footer-links {
    padding-top: 20px;
  }

  .footer-links-link > a,
  .social-media-links > a {
    margin-bottom: 20px;
  }
}

/* Mobile Mid */
@media only screen and (max-width: 470px) {
  .header-main {
    text-align: left;
  }

  .header-main > .main {
    font-size: 36px;
    letter-spacing: -1px;
    width: 100%;
  }

  .header-main > .intro {
    font-size: 18px;
  }

  .header-main > .biography {
    width: 100%;
    font-size: 17px;
    font-weight: 400;
    letter-spacing: 0px;
  }

  .lang-btn {
    display: none;
  }

  .lang-xs-btn {
    display: block;
    margin: 0px;
  }

  .skills > .skills-header {
    font-size: 35px;
    text-align: left;
  }

  .skills > .skills-text {
    font-size: 13px;
    width: 100%;
    line-height: 1.6rem;
    padding: 10px 0;
    text-align: left;
  }

  .skill-list {
    padding: 30px 0;
  }

